<template>
    <yd-layout>
        <div v-if="firstFlag" v-show="treeFlag">
            <div class="tree-menu">
                <trs-tree :type="type" v-on:callbackTop="ShareMeeting" @click="jiekouFlag = !jiekouFlag;"></trs-tree>
            </div>
            <!-- <div class="submit" @click="treeFlag = !treeFlag">确定</div> -->
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#fff" v-show="treeFlag">
            <yd-button v-on:click.native="share()" size="large" type="primary" bgcolor="#00B6FC" color="#fefefe">确定</yd-button>
        </yd-tabbar>
        <div v-show="!treeFlag">
            <div class="mainContentFluid details">
                <div class="fluidWidth">
                    <yd-flexbox>
                        <div class="depMeet" v-text="Detail.meetType"></div>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="meetTiyle" v-text="Detail.meettitle"></div>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="meetDetail">会议时间：</div>
                        <yd-flexbox-item v-text="Detail.time"></yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="meetDetail">会议地点：</div>
                        <yd-flexbox-item v-text="Detail.meetAddress"></yd-flexbox-item>
                    </yd-flexbox>
                    <yd-flexbox>
                        <div class="meetDetail">会议提醒：</div>
                        <yd-flexbox-item v-text="">提前{{Detail.remind}}分钟</yd-flexbox-item>
                    </yd-flexbox>
                </div>
            </div>
            <div class="mainContentFluid twoPart">
                <div class="fluidWidth">
                    <yd-flexbox @click.native="attender">
                        <div class="icon"><img src="../../../static/images/conference/4.png"></div>
                        <yd-flexbox-item>参会人</yd-flexbox-item>
                        <div class="rate" v-text="conPp+'/'+Pp"></div>
                        <div class="leftSelect">人确认参会</div>
                        <div class="nextIcon">
                            <router-link to="#" slot="right">
                                <yd-navbar-next-icon></yd-navbar-next-icon>
                            </router-link>
                        </div>
                    </yd-flexbox>
                    <!-- <yd-flexbox>
                        <div class="icon"><img src="../../../static/images/conference/5.png"></div>
                        <yd-flexbox-item>会议分享</yd-flexbox-item>
                        <div class="leftSelect" @click="treeFlag = !treeFlag">暂未选择分享人员</div>
                        <div class="nextIcon">
                            <router-link to="#" slot="right">
                                <yd-navbar-next-icon></yd-navbar-next-icon>
                            </router-link>
                        </div>
                    </yd-flexbox> -->
                    <div class="sharePerson">
                        <div class="shareLeft">会议分享</div>
                        <div class="shareRight" @click="treeFlag = !treeFlag;firstFlag=true" v-show="noSharePerson==''">暂未选择分享人员</div>
                        <div class="shareRight" @click="treeFlag = !treeFlag" v-show="noSharePerson!=''">{{noSharePerson}}</div>
                    </div>
                </div>
            </div>
            <!-- <div v-show="isHide">
                <div class="mainContentFluid thirdPart">
                    <div class="fluidWidth" @click="codeSign()">
                        <yd-flexbox direction="vertical">
                            <div>签到码：向参会者展示此二维码</div>
                            <div>提前30分钟扫码签到</div>
                            <div class="starTime" v-text="'开始时间：' + Detail.time"></div>
                        </yd-flexbox>
                        <div class="Qr_code"><img :src="conf.qrCodeURL+Detail.img" /></div>
                    </div>
                </div>
            </div> -->
        </div>
        <yd-tabbar slot="tabbar" bgcolor="#f8f8f8" v-show="detailType && !treeFlag && isHide">
            <div class="tabBtn">
                <div class="threeBtn btn1" @click="endMeet()">结束会议</div>
                <div class="threeBtn btn2" @click="editMore()">编辑更多</div>
                <div class="threeBtn btn3" @click="cancel()">取消预定</div>
            </div>
        </yd-tabbar>
    </yd-layout>
</template>
<script>
import { mapGetters, mapMutations } from 'vuex';
import trsTree from '../../components/util/trsTree/trsTree.vue';
export default {
    components: {
        trsTree
    },
    data() {
        return {
            searchFlag: false,
            type: 'share',
            jiekouFlag: false,
            treeFlag: false,
            con_url: '',
            Detail: [],
            meetId: '',
            time: '', // 处理后的时间
            startTime: '', // 会议开始时间
            getTime: '', // 开始时间的时间戳
            address: '',
            title: '',
            person: '',
            timeArr: [],
            currentTime: '', // 当前时间，用来对比开始时间
            isHide: false,
            detailType: this.$route.params.detype === 'true' || this.$route.params.detype === true,
            conPp: 0,
            Pp: 0,
            shareId: '',
            noSharePerson: '',
            firstFlag: false // 第一次加载树
        }
    },
    mounted() {
        document.setTitle('会议室详情');
        this.initInfo();
        console.log(this.$route.params.detype);
    },
    computed: {
        ...mapGetters(['getUserInfo', 'getMeetingInfo'])
    },
    methods: {
        ...mapMutations([
            'setMeetingInfo'
        ]),
        ShareMeeting(json) {
            console.log(json);
            let vm = this;
            if (vm.shareId == '') {
                let idArr1 = [];
                let nameArr1 = [];
                for (let key in json) {
                    idArr1.push(key);
                    for (let key1 in json[key]) {
                        nameArr1.push(key1);
                        for (let i = 0; i < idArr1.length; i++) {
                            if (idArr1[i] == key && json[key][key1] == 0) {
                                idArr1.splice(i, 1);
                                nameArr1.splice(i, 1);
                            }
                        }
                    }
                }
                vm.shareId = idArr1.join(',');
                vm.noSharePerson = nameArr1.join('、');
            } else {
                let idArr = [];
                idArr = vm.shareId.split(',');
                let nameArr = [];
                nameArr = vm.noSharePerson.split('、');
                for (let key in json) {
                    idArr.push(key);
                    idArr = Array.from(new Set(idArr));
                    for (let key1 in json[key]) {
                        for (let i = 0; i < idArr.length; i++) {
                            if (idArr[i] == key && json[key][key1] == 0) {
                                idArr.splice(i, 1);
                            }
                        }
                    }
                }
                for (let nkey in json) {
                    for (let nkey1 in json[nkey]) {
                        nameArr.push(nkey1);
                        nameArr = Array.from(new Set(nameArr));
                        for (let i = 0; i < nameArr.length; i++) {
                            if (nameArr[i] == nkey1 && json[nkey][nkey1] == 0) {
                                nameArr.splice(i, 1);
                            }
                        }
                    }
                }
                vm.shareId = idArr.join(',');
                vm.noSharePerson = nameArr.join('、');
            }
            console.log(vm.shareId);
            console.log(vm.noSharePerson);
        },
        // 会议分享
        share() {
            let vm = this;
            if (vm.shareId == '') {
                // vm.$dialog.toast({
                //     mes: '请选择分享人员',
                //     timeout: 2000
                // });
                vm.treeFlag = false;
            } else {
                let promise = new Promise(function (resolve, reject) {
                    vm.get(vm.conf.uchatMessage + '/sendMessageApi', {
                        'token': `${vm.getUserInfo.ssotoken}L/wp-2q${vm.getUserInfo.ssotokenCode}`,
                        'title': '会议分享',
                        'summary': '会议主题：' + vm.Detail.meettitle + '\n' + '\n' + '会议时间：' + vm.Detail.time + '\n' + '\n' + '会议地点：' + vm.Detail.meetAddress,
                        'url': '',
                        'empcode': vm.shareId
                    }, function (res) {
                        vm.$dialog.toast({
                            mes: '分享成功',
                            timeout: 2000
                        });
                        resolve(true);
                    })
                });
                promise.then(function (data) {
                    if (data) {
                        vm.treeFlag = !vm.treeFlag;
                    }
                });
            }
        },
        // 获取会议详情
        initInfo() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/detail', {
                'name': vm.getUserInfo.ssotoken,
                'mid': vm.$route.params.meetId
            }, function (res) {
                vm.Detail = res.jsonData;
                console.log(vm.Detail.conMeetPeople);
                vm.conPp = vm.Detail.conMeetPeople == 'undefined' ? 0 : vm.Detail.conMeetPeople;
                vm.Pp = vm.meetPeople == 'undefined' ? 0 : vm.Detail.meetPeople;
                vm.time = vm.Detail.time == '' ? '' : vm.Detail.time.split(' ')[0];
                vm.startTime = vm.Detail.time.split(' -')[0];
                vm.Detail.isHide == true ? vm.isHide = true : vm.isHide = false;
                vm.setMeetingInfo({
                    meetId: vm.Detail.id,
                    meetTitle: vm.Detail.meettitle,
                    meetType: vm.Detail.meetType,
                    meetAddress: vm.Detail.meetAddress,
                    meetTime: vm.Detail.time,
                    meetPeople: vm.Detail.meetPeople,
                    conMeetPeo: vm.Detail.conMeetPeople,
                    meetRemind: vm.Detail.remind
                })
            })
        },
        // 编辑更多
        editMore() {
            let vm = this;
            vm.$router.push({
                name: 'appointment',
                params: {
                    flag: true,
                    meetId: vm.$route.params.meetId
                }
            })
        },
        // 取消预定
        cancel() {
            let vm = this;
            vm.get(vm.conf.portalMobile + '/API/MeetManager/meet/cannel', {
                'name': vm.getUserInfo.ssotoken,
                'mid': vm.$route.params.meetId
            }, function (res) {
                vm.$router.push('/resHistory');
            })
        },
        // 结束会议
        endMeet() {
            let vm = this;
            vm.currentTime = Number(new Date());
            vm.getTime = vm.getTimeReverse(vm.startTime);
            if (vm.getTime > vm.currentTime) {
                vm.$dialog.toast({
                    mes: '会议还未开始不能结束',
                    timeout: 2000
                });
            } else {
                vm.post(vm.conf.portalMobile + '/API/MeetManager/meet/endMeet', {
                    'name': vm.getUserInfo.ssotoken,
                    'roomId': vm.$route.params.meetId
                }, function (res) {
                    vm.$router.push('/resHistory');
                })
            }
        },
        // 参会人
        attender() {
            let vm = this;
            vm.$router.push({
                name: 'attendee',
                params: {
                    meetId: vm.$route.params.meetId,
                    flag: true
                }
            })
        },
        // 二维码点击签到
        codeSign() {
            let vm = this;
            vm.$router.push({
                name: 'meetingSign',
                params: {
                    time: vm.Detail.time,
                    title: vm.Detail.meettitle,
                    codeImg: vm.Detail.img,
                    conPp: vm.Detail.conMeetPeople,
                    Pp: vm.Detail.meetPeople
                }
            })
        },
        // 获取时间的时间戳2017-11-15 11:15
        getTimeReverse(date) {
            let meetBeginTime = new Date(Date.parse(date.replace('-', '/'))).getTime();
            return meetBeginTime;
        }
    }
}
</script>
<style scoped>
.depMeet {
    font-size: 0.26rem;
    height: .39rem;
    width: 1.49rem;
    background-color: #ffb330;
    color: #fff;
    text-align: center;
    border-radius: 0.05rem;
}

.submit {
    background-color: #fff;
    border-bottom: 1px dashed #c1baba;
    line-height: 1rem;
    text-align: center;
}

.meetTiyle {
    font-size: 0.3rem;
    color: #333;
}

.icon img {
    width: 0.29rem;
    height: 0.29rem;
    margin-top: 0.07rem;
}

.icon {
    padding-right: 0.26rem;
}

.twoPart .yd-flexbox {
    font-size: 0.3rem;
    border-bottom: 1px solid #ccc;
    line-height: 0.8rem;
    color: #333;
}

.twoPart .yd-flexbox:last-child {
    border-bottom: none;
}

.twoPart .yd-back-icon:before,
.yd-next-icon:before {
    padding-left: 0.3rem;
    font-size: 0.18rem;
    margin-right: 0.35rem;
}

.rate {
    color: #369df4;
    padding-right: 0.1rem;
}

.leftSelect,
.starTime {
    color: #999;
}

.Qr_code {
    text-align: center;
    margin-top: 0.3rem;
    padding-bottom: 0.2rem;
}

.bottomBtn {
    bottom: 0;
    right: 0;
    width: 100%;
    background-color: #F2F1EF;
}

.sharePerson {
    width: 100%;
    min-height: .8rem;
    font-size: .3rem;
    line-height: .8rem;
    overflow: hidden;
    background: url(../../../static/images/arrow@.png) no-repeat right;
    background-size: .34rem;
}

.shareLeft {
    float: left;
    background: url(../../../static/images/conference/5.png) no-repeat left;
    background-size: .29rem;
    padding-left: .55rem;
}

.shareRight {
    float: left;
    width: 4.65rem;
    text-align: right;
    color: #999;
    line-height: .5rem;
    margin-top: .14rem;
    padding-left: .3rem;
}

.yd-tabbar {
    padding: 0;
}

.yd-tabbar:after {
    border-top: none;
}

.yd-btn-block {
    height: 1rem;
    font-size: 0.3rem !important;
    margin-top: 0;
    border-radius: 0;
}

.threeBtn {
    width: 2.12rem;
    height: .83rem;
    text-align: center;
    color: #fff;
    font-size: .3rem;
    border-radius: 5px;
    line-height: .85rem;
    display: inline-block;
}

.btn1 {
    margin-right: .1rem;
    background-color: #59a5fd;
}

.btn2 {
    margin-right: .1rem;
    background-color: #ff4677;
}

.btn3 {
    margin-right: .1rem;
    background-color: #FFC121;
}

.tabBtn {
    padding-top: .27rem;
    padding-bottom: .27rem;
    width: 100%;
    text-align: center;
}
</style>


